import Men from "../men/Men";
import Women from "../women/Women";
import LandingPage from "../landingPage/LandingPage";
import styles from "./Container.module.css";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { animeActions } from "../../store/animeSlice";

const Container = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(
      animeActions.contentContainerHeight(
        document.getElementById("ContentContainer").offsetHeight
      )
    );
    dispatch(
      animeActions.halfContainerHeight(
        document.getElementById("ContentContainer").offsetHeight / 2
      )
    );
  }, [dispatch]);

  return (
    <div id="ContentContainer" className={styles.contentContainer}>
      <div className={styles.genderSelector}>
        <Men />
        <Women />
      </div>
      <LandingPage />
    </div>
  );
};

export default Container;
